// 历史分析记录
import request from '@/interceptor.js';

/**
 * 新增或更新分析记录
 *
 * @export
 * @param {*} [data={}]
 * @returns
 */
export function saveAnalysis(data = {}) {
  return request({
    method: 'post',
    url: '/rc-analyse/save/analysis',
    data
  });
}

/**
 * 查询某条历史分析记录详情
 *
 * @export
 * @param {string} [id=''] 分析记录ID
 * @returns promise
 */
export function queryAnalysisDetail(id = '') {
  return request({
    method: 'get',
    url: `/rc-analyse/query/analysis/${id}`
  });
}

/**
 * 获取历史分析记录列表
 *
 * @export
 * @param {string} [moduleName=''] 名称
 * @param {number} [page=1] 当前页
 * @param {number} [size=10] 每页显示的总条数
 * @returns promise
 */
export function getAnalysisList(moduleName = '', page = 1, size = 10) {
  return request({
    method: 'post',
    url: `/rc-analyse/query/analysis?page=${page}&size=${size}`,
    data: {
      moduleName
    }
  });
}

export function queryVarAutoSplit(moduleName = '', vbName = '') {
  return request({
    method: 'post',
    url: `/rc-analyse/Query/VaiableAutoSplitLines`,
    data: {
      moduleName,
      vbName
    }
  });
}
