<template>
  <div class="records">
    <el-card>
      <div slot="header">
        <span class="title">历史分析记录</span>
        <el-popover placement="right" width="500" trigger="hover">
          <div>
            <p>记录所有单变量和多变量分析的历史分析记录，方便您回顾查看</p>
          </div>
          <i class="el-icon-question" slot="reference"></i>
        </el-popover>
      </div>
      <div class="frame-fun clear">
        <span class="total-samples">共 {{total}} 个文件</span>
        <el-input placeholder="请输入" prefix-icon="el-icon-search" v-model="moduleName">
          <el-button slot="append" @click.stop="getRecordList(moduleName)" type="primary">搜索</el-button>
        </el-input>
      </div>
      <div class="wrap-table-comm clear">
        <el-table :data="recordList" stripe style="width: 100%" :header-row-style="headerStyle">
          <el-table-column type="index" label="序号" width="50" align="center"></el-table-column>
          <el-table-column prop="name" label="文件名称" align="center"></el-table-column>
          <el-table-column prop="type" label="分析类型" align="center" :formatter="formatter"></el-table-column>
          <el-table-column prop="createtime" label="保存日期" align="center"></el-table-column>
          <el-table-column prop="parmDes" label="参数说明" align="center"></el-table-column>
          <el-table-column label="操作" align="center" width="80px">
            <template slot-scope="scope">
              <a href @click.stop.prevent="goToDetail(scope.row)" class="btn-view">查看</a>
              <!-- <router-link class="btn-view" :to="{'path': `/${scope.row.type===1 ? 'multivariate' : 'univariate'}/view/${scope.row.id}`}">查看</router-link> -->
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          v-if="recordList.length"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="page"
          :page-sizes="sizes"
          :page-size="size"
          layout="prev, pager, next, sizes"
          :total="total"
        ></el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
import {getAnalysisList} from '@/api/analysis.js';
export default {
  name: 'page-records',
  data() {
    return {
      page: 1,
      size: 10,
      total: 0,
      sizes: [10, 20, 50, 100],
      moduleName: '',
      headerStyle: {
        color: '#409eff'
      },
      recordList: []
    };
  },
  created() {
    this.getRecordList(this.moduleName);
  },
  mounted() {},
  methods: {
    formatter(row) {
      return this.$options.filters.analyseType(row.type);
    },
    getRecordList(moduleName = '') {
      getAnalysisList(moduleName, this.page, this.size)
        .then(result => {
          const data = result.result;
          this.total = data.totalNum || 0;
          if (Array.isArray(data.items)) {
            this.recordList = data.items;
            return;
          }
          this.recordList = [];
        })
        .catch(err => {
          this.recordList = [];
          this.total = 0;
          console.log(err);
        });
    },
    handleSizeChange(val) {
      this.size = val;
      this.page = 1;
      this.getRecordList(this.moduleName);
    },
    handleCurrentChange(val) {
      this.page = val;
      this.getRecordList(this.moduleName);
    },
    goToDetail(item) {
      console.log(item);
      let link;
      switch (item.type) {
        case 0:
          link = `/univariate-view/${item.id}`;
          this.$router.push(link);
          break;
        case 1:
          link = `/multivariate-view/${item.id}`;
          this.$router.push(link);
          break;
        case 2:
          link = `/univariate-monitor/${item.id}`;
          this.$router.push(link);
          break;
        case 3:
          link = `/decision-monitor/${item.id}`;
          this.$router.push(link);
          break;
        default:
          break;
      }
    }
  }
};
</script>

<style scoped>
.el-card {
  margin: 20px 15px;
}
.thead {
  background: #409eff;
  color: #fff;
}
.el-input {
  float: right;
  width: 300px;
}
</style>
